.comp {
  padding-top: 50px;

  &__bread {
    margin-bottom: 25px;
  }

  &__title {
    margin-bottom: 30px;
  }

  &__about {
    margin-bottom: 180px;
  }

  &__advs {
    padding: 60px 0;
    background: url("../images/bg/ispecsBg.png") 0 0;
  }

}

.cabout {
  &__left,&__right {
    @include col;
    @include size(12);
  }

  &__wrap {
    border-radius: 6px;
    border: 1px solid rgba($text,.2);
    padding: 10px;
    position: relative;
  }

  &__img {
    width: 130%;
    position: absolute;
    top: 100%;
    left: 100%;
    max-width: 765px;
    transform: translate(-30%,-50%);
  }

  &__list {
    background: url("../images/bg/ispecsBg.png") 0 0;
    padding: 50px 35px 50px 0;
    overflow: hidden;
  }

  &__item {
    display: flex;
    align-items: center;
    margin-bottom: 30px;

    &:last-child {
      margin-bottom: 0;
    }

    &-num {
      text-shadow:0px 1px 0 rgb(236,175,137),0px -1px 0 rgb(236,175,137),1px 0px 0 rgb(236,175,137),-1px 0px 0 rgb(236,175,137),1px 1px 0 rgb(236,175,137),1px -1px 0 rgb(236,175,137),-1px 1px 0 rgb(236,175,137),-1px -1px 0 rgb(236,175,137);
      -webkit-text-shadow:0px 1px 0 rgb(236,175,137),0px -1px 0 rgb(236,175,137),1px 0px 0 rgb(236,175,137),-1px 0px 0 rgb(236,175,137),1px 1px 0 rgb(236,175,137),1px -1px 0 rgb(236,175,137),-1px 1px 0 rgb(236,175,137),-1px -1px 0 rgb(236,175,137);
      -moz-text-shadow:0px 1px 0 rgb(236,175,137),0px -1px 0 rgb(236,175,137),1px 0px 0 rgb(236,175,137),-1px 0px 0 rgb(236,175,137),1px 1px 0 rgb(236,175,137),1px -1px 0 rgb(236,175,137),-1px 1px 0 rgb(236,175,137),-1px -1px 0 rgb(236,175,137);
      color: $bg;
      font-family: "PT Serif";
      font-size: 110px;
      font-weight: 700;
      position: relative;
      left: rem(-28px);
      line-height: 1;
    }

    &-desc {
      color: rgba($green,.8);
      position: relative;
      padding-bottom: 12px;

      &:after {
        content: '';
        display: block;
        position: absolute;
        top: 100%;
        left: 0;
        height: 2px;
        width: rem(82px);
        background-color: $lightGreen;
      }
    }
  }

  &__title {
    font-family: "PT Serif";
    font-size: 36px;
    margin-bottom: 15px;
  }

  &__desc {
    color: $orange;
    font-size: 24px;
    margin-bottom: 25px;
  }

  &__link {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 264px;
    height: 54px;
    border: 1px solid $green;
    border-radius: 4px;
    transition: .3s;

    &:hover {
      border: 1px solid $orange;
      .cabout__link {

        &-svg {
          fill: $orange;
        }
        &-title {
          color: $orange;
        }
      }
    }

    &-svg {
      width: rem(22px);
      height: rem(19px);
      fill: $green;
      margin-right: rem(10px);
      transition: .3s;
    }

    &-title {
      font-size: rem(18px);
      font-weight: 500;
      color: $green;
      transition: .3s;
    }
  }
}

.cvideo{
  background-color: $text;
  padding-top: 60px;
  padding-bottom: 80px;

  &__title {
    margin-bottom: 20px;
  }

  &__video {
    @include col;
    @include size(20);
    @include shift(2);
    position: relative;
    padding: 10px;
    border-radius: 6px;
    border: 1px solid $bg;
  }

  &__wrap{
    position: absolute;
    top: 32px;
    bottom: 32px;
    left: 32px;
    right: 32px;
    border-radius: 6px;
    overflow: hidden;
  }

  &__link {
    position: relative;
    display: block;
    &:hover {
      .cvideo__button {
        border: 1px solid $orange;
      }
    }
  }

  &__button {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: rem(-55px);
    margin-left: rem(-55px);
    width: rem(110px);
    height: rem(110px);
    border: 1px solid $white;
    border-radius: 50%;
    padding: rem(4px);
    transition: .3s;



    &:before {
      content: '';
      display: block;
      border-radius: 50%;
      background-color: $white;
      width: 100%;
      height: 100%;
    }

    &:after {
      content: '';
      display: block;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: rem(11.5px) 0 rem(11.5px) rem(20px);
      border-color: transparent transparent transparent $orange;
      position: absolute;
      top: 50%;
      left: 50%;
      margin-left: rem(-10px);
      margin-top: rem(-11.5px);
    }
  }
}

.ccerts {
  background-color: $text;
  background-image: url("/images/bg/ifeedBg.png");
  background-repeat: repeat-x;
  background-position: 0 bottom;
  padding-top: 60px;
  padding-bottom: 180px;

  &__title {
    margin-bottom: 20px;
  }

  &__item {
    @include col;
    @include size(8);
  }
}